@import url(https://fonts.googleapis.com/css2?family=Lora:wght@400;600);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400&);
@import url(https://use.fontawesome.com/releases/v5.13.1/css/all.css);
.br {
  width: 100%;
}

header {
  background: var(--red);
  color: var(--white);
}
header > section > * {
  display: flex;
  justify-content: center;
  align-items: center;
}
header a {
  color: var(--white);
  text-decoration: none;
}
header a * {
  font-family: var(--sans), sans-serif;
}
#icons {
  font-size: 2em;
}
#icons > svg {
  height: 1em;
}
#title {
  font-size: 1.5em;
}
#view_on_github {
  position: absolute;
  right: 10px;
  top: 10px;
  font-size: 0.8em;
}
#view_on_github i {
  margin-right: 5px;
}

.cell {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  max-width: 100%;
  margin: 10px;
  text-align: center;
}
.header {
  font-weight: 600;
  height: 20px;
}
.header:not(:only-child) {
  margin-bottom: 8px;
}
.control {
  min-height: 30px;
}
.header,
.control {
  display: flex;
  justify-content: center;
  align-items: center;
}
.tiny_control {
  width: 20px;
  height: 20px;
}
.small_control {
  width: 100px;
  height: 30px;
}
.square_control {
  width: 30px;
  height: 30px;
  padding: 0;
}
.big_control {
  width: 200px;
  height: 60px;
}

@media screen and (max-width: 900px) {
  .header {
    height: unset;
  }
}

[data-tooltip] {
  position: relative;
}
[data-tooltip]:after {
  box-sizing: border-box;
  content: attr(data-tooltip);
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  left: 50%;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  max-width: 260px;
  bottom: 100%;
  -webkit-transform: translate(-50%, -8px);
          transform: translate(-50%, -8px);
  padding: 8px 12px;
  border-radius: 2px;
  background: var(--black);
  color: var(--white);
  opacity: 0;
  z-index: 2;
  pointer-events: none;
  -webkit-transition: opacity 0.25s ease;
  transition: opacity 0.25s ease;
  -webkit-transition-delay: 0.1s;
          transition-delay: 0.1s;
}
[data-tooltip]:hover:after {
  opacity: 1;
}

#upload_button[data-drag='true'] {
  border: dashed 2px black;
}
#upload_button > span {
  pointer-events: none;
}

#canvas_frame {
  display: inline-block;
  outline: solid 1px var(--black);
  max-width: 100%;
  max-height: 90vh;
  overflow-x: auto;
  overflow-y: auto;
  background-image: url(/svg-to-png/static/media/checkers.b107f568.svg);
  background-size: 2vmin;
  background-attachment: fixed;
}

section {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
  width: 100%;
  max-width: var(--width);
  margin: 30px auto;
  padding: 0 20px;
}

@media screen and (max-width: 900px) {
  section {
    flex-direction: column;
    align-items: center;
  }
}

hr {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
  width: 100%;
  max-width: var(--width);
  height: 1px;
  margin: 30px auto;
  padding: 0 20px;
  border: none;
  background: var(--light-gray);
  background-clip: content-box;
}

main {
  flex-grow: 1;
}

* {
  --black: #000000;
  --light-gray: #e8e8e8;
  --gray: #808080;
  --white: #ffffff;
  --red: #e91e63;
  --serif: 'Lora', serif;
  --sans: 'Lato', sans-serif;
  --width: 900px;

  font-family: var(--serif), serif;
}
body {
  margin: 0;
}
#root {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
}

a {
  color: var(--red);
}
a:hover,
a:focus {
  color: var(--black);
}
button,
input[type='checkbox']:not([disabled]),
input[type='color']:not([disabled]) {
  cursor: pointer;
  background: var(--light-gray);
}
button,
input,
textarea,
select,
option {
  margin: 0;
  font-size: 1em;
  border: solid 1px var(--gray);
  outline: none;
}
button {
  padding: 0 10px;
}
input,
textarea {
  padding: 0 5px;
}
button:focus,
input:focus,
textarea:focus,
select:focus {
  border-color: var(--red);
  box-shadow: 0 0 2px 0 var(--red);
}
svg:not(:first-child),
i:not(:first-child) {
  margin-left: 8px;
}
svg:not(:last-child),
i:not(:last-child) {
  margin-right: 8px;
}
[disabled] {
  opacity: 0.2;
}

